<template>
<div>
  <section class="flex-table">
    <div>
      <div class="table-row table-header">
        <div class="table-col" v-for="col in columns" :key="col">
          <span>{{col | capitalize}}</span>
          <span class="table-sort" v-if="hasSort(col)">
            <em class="ion-android-arrow-dropup" v-on:click="sortList(col, 'asc')"></em>
            <em class="ion-android-arrow-dropdown" v-on:click="sortList(col, 'desc')"></em>
          </span>
        </div>
      </div>
      <div class="flex-table-body" v-if="hasData">
        <div class="table-row" v-for="(row, index) in finalData()" :key="index" v-on:scroll.passive="handleScroll($event)">
          <div class="table-col" v-for="col in columns" :key="col">{{row[col]}}</div>
        </div>
      </div>
      <div v-else class="flex-table-body">
        <h3 class="text-center">No data</h3>
      </div>
    </div>
  </section>  
</div>
</template>

<script>
import _ from 'lodash';
export default {
  name: 'VueFlexTable',
  props: {
    data: {
      type: Array,
      required: true
    },
    sortBy: {
      type: Array,
      required: false
    }
  },
  filters: {
    capitalize: function (value) {
      if (!value) return '';
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    }
  },
  data () {
    return {
      sort_by: 'id',
      sort_type: 'asc',
      hasData: true,
      checkAllGroup: []
    };
  },
  computed: {
    columns () {
      return Object.keys(this.data[0]);
    }
  },
  methods: {
    finalData () {
      let data = _.orderBy(this.data, [this.sort_by], [this.sort_type]);
      this.hasData = data.length > 0;
      return data;
    },
    hasSort (col) {
      return this.sortBy.indexOf(col) > -1;
    },
    sortList (sortItem, sortType) {
      this.sort_by = sortItem;
      this.sort_type = sortType;
    },
    handleScroll ($event) {
      this.$emit('handlScroll', $event);
    }
  }
}
</script>

